.App {
  text-align: center;
}

.App-header {
  display: flex;
  flex-wrap: wrap;
  background-color: #282c34;
  height: 100vh;
}

.box-container{
  display: flex;
  width:100%;
}

.box{
  height: 100px;
  width: 100%;
}

#pink{
  background-color: hotpink;
}

#blue{
  background-color: dodgerblue;
}

#orange{
  background-color: orange;
}

.displayed-card{
  background-color: #282c34;
  color: white;
  font-size: 16px;
  border: solid 2px transparent;
  width: 100%;

}

.title{
  font-size:20px;
  font-weight: bold;
}

.description{

}


.shake-button {
  height: 50px;
  width: 100%;
  background-color: white;
  color: black;
  font-size: 16px;
  position: absolute;
  bottom: 0;
  left: 0;
}

